<template>
	<view class="mian">
			<view class="scrch">
				<input type="text" name="" id="" value="" placeholder="搜索关键字"/>
				<text class="iconfont icon-sousuo"></text>
			</view>
			<view class="information">
				<view class="information-data" v-for="(item,index) in informationlist" :key="index">
					<view class="text">
						<view class="txt">{{item.title}}</view>
						<view class="time">{{item.addtime | formattime}}</view>
					</view>
					<image v-if="item.picture" class="img" :src="$config.imgUrl+item.picture" mode=""></image>
				</view>
			</view>
					
	</view>
</template>

<script>
	import moment from 'moment'
	export default {
		data() {
			return {
				informationlist:[],
			};
		},
		filters:{
			formattime(val){
				return moment(val).format("YYYY-MM-DD")
			}
		},
		onLoad(option){
			this.getinformationlist(option.type)
			if(option.type==1){
				uni.setNavigationBarTitle({
				    title: '公告'
				});
			}else{
				uni.setNavigationBarTitle({
				    title: '资讯'
				});
			}
		},
		methods:{
			getinformationlist(type){
				uni.showLoading({
					title:'加载中...'
				})
				this.$http.get('/api/news/list',{type:type}).then(res=>{
					if(res.errCode==10000){
						this.informationlist= res.data.list
						uni.hideLoading()
					}
				},fail=>{
					console.log(fail)
				})
			},
		}
	}
</script>
<style lang="less" scoped>
	.mian {
		height: 93vh;
	}
		.scrch{
			width: 90%;
			height: 82rpx;
			background-color: #f4f6f6;
			border-radius: 50rpx;
			margin: 30rpx auto 0 auto;
			display: flex;
			align-items: center;
			justify-content: space-around;
			input{
				width: 70%;
				font-size: 24rpx;
				color: #455154;
			}
			.iconfont{
				width: 30rpx;
				height: 30rpx;
			}
		}
	.information {
		width: 90%;
		margin: 50rpx auto;
		display: flex;
		flex-direction: column;
		
		.information-data {
			display: flex;
			justify-content: space-between;
			margin-bottom: 50rpx;
			.text {
				flex: 1;
				padding-right: 20rpx;
				height: 160rpx;
				font-size: 26rpx;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				.txt {
					width: 100%;
					display:-webkit-box;
					overflow:hidden;
					text-overflow:ellipsis;
					word-break: break-all;
					-webkit-line-clamp:3;
					-webkit-box-orient:vertical;
				}
				.time {
					color: #afafaf;
				}
			}
			.img {
				width: 245rpx;
				height: 160rpx;
				border-radius: 15rpx;
			}
		}
	}
</style>

